import Key from './Key';

const keycodes = {
  LEFT: 37, // arrow left
  RIGHT: 39, // arrow right
  UP: 38, // arrow up
  DOWN:40 // arrow down
}

class Input {
  constructor() {
    this.key = {
      left: new Key(keycodes.LEFT),
      right: new Key(keycodes.RIGHT),
      up: new Key(keycodes.UP),
      down: new Key(keycodes.DOWN)
    }
  }

}

export default Input;


