class Key {
  constructor(code) {
    this.code = code;
    this.isDown = false;
    this.isUp = true;
    this.press = null;
    this.release = null;

    document.addEventListener('keydown', this.handleKeyDown.bind(this));
    document.addEventListener('keyup', this.handleKeyUp.bind(this));  
  }

  handleKeyDown(e) {
    e.preventDefault();
    if (this.code == e.keyCode) {
      if (this.isUp && this.press) {
        this.press();
      }

      this.isDown = true;
      this.isUp = false;
    }
  }

  handleKeyUp(e) {
    e.preventDefault();
    if (this.code == e.keyCode) {
      if (this.isDown && this.release) {
        this.release();
      }

      this.isDown = false;
      this.isUp = true;
    }
  }
}

export default Key;


